.is-hidden {
  opacity: 0;
}

.gallery {
  flex-wrap: wrap;
  gap: 10px;
  margin: 0 auto;
  padding: 60px 100px;
  display: flex;
}

.photo-card {
  background-color: rgba(212, 212, 199, .3);
  border-radius: 0 0 6px 6px;
  margin: 0 auto;
  box-shadow: 0 15px 40px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.load-more {
  color: #fff;
  min-width: 150px;
  height: 40px;
  background-color: rgba(12, 63, 70, .714);
  border: 3px solid;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin: 0 auto 30px;
  display: flex;
}

.load-more:hover, .load-morefocus, .form-button:hover, .form-button:focus {
  background-color: rgba(42, 185, 130, .565);
  border: 3px solid;
}

.form-input:hover {
  border: 2px solid #fff;
}

.form-input:focus {
  background-color: #fff;
  border: 3px solid #fff;
}

img {
  border-radius: 0 0 6px 6px;
}

.info {
  display: flex;
}

.info-item {
  color: #000;
  flex-direction: column;
  align-items: center;
  padding-left: 15px;
  display: flex;
}

.search-form {
  min-height: 100px;
  background-color: rgba(8, 231, 194, .443);
  justify-content: center;
  align-items: center;
  margin-top: 25px;
  display: flex;
}

.form-input {
  min-width: 400px;
  height: 40px;
  border: 3px solid;
  border-radius: 10px;
  outline: none;
  padding-left: 10px;
}

.form-button {
  color: #fff;
  min-width: 150px;
  height: 40px;
  background-color: rgba(12, 63, 70, .714);
  border-radius: 10px;
  margin-left: 30px;
}

